html {
  scroll-behavior: smooth;
}
body {
  background-color: #1a1142;
}
.main.index {
  position: relative;
  background-color: #1a1142;
  overflow: hidden;
}
.main.index::before {
  content: '';
  position: absolute;
  top: 470px;
  left: calc(50% - 300px);
  width: 630px;
  height: 630px;
  filter: blur(1200px);
  width: 600px;
  height: 600px;
  background-color: #E5A7D0;
  opacity: 0.9;
}
.main.presale {
  background-color: #1a1142;
  overflow: hidden;
}
@media all and (min-width: 1025px) {
  .header.presale .header__body {
    justify-content: center;
    position: relative;
  }
  .header.presale .header__button {
    position: absolute;
    right: 0;
  }
}
@media all and (max-width: 1024px) {
  .header.presale .header__body {
    flex-direction: column;
    align-items: center;
  }
  .header.presale .header__logo {
    margin-bottom: 40px;
  }
  .header.presale .header__button {
    display: block;
  }
}
.header .header__body {
  display: flex;
  max-width: 1350px;
  margin: 0 auto;
  padding-top: 20px;
}
.header .header__logo {
  display: flex;
  align-items: center;
}
.header .header__logo .image {
  width: 46px;
  height: auto;
}
.header .header__logo-image {
  position: relative;
}
.header .header__logo-image img {
  min-width: 194px;
  max-width: 194px;
  height: auto;
}
.header .header__logo-image::before {
  content: '';
  width: 80px;
  height: 80px;
  position: absolute;
  top: -20px;
  left: -20px;
  background: radial-gradient(46.06% 46.06% at 50% 50%, #3D2796 0%, rgba(63, 40, 154, 0) 100%);
  opacity: 0.39;
}
.header .header__nav-items {
  display: flex;
}
.header .header__nav-item {
  margin: 0 15px;
}
.header .header__nav-item a {
  color: #fff;
  text-decoration: none;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  opacity: 0.9;
}
.header .header__languages {
  position: relative;
}
.header .header__languages-icon {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #fff;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  cursor: pointer;
  user-select: none;
}
.header .header__languages-icon svg {
  width: 10px;
  height: auto;
  margin-left: 10px;
}
.header .header__languages-icon img {
  height: 12px;
  width: auto;
  display: block;
  margin-left: 10px;
}
.header .header__languages-list {
  position: absolute;
  top: 100%;
  right: 0;
  border-radius: 5px;
  box-sizing: border-box;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
  padding-top: 12px;
}
.header .header__languages-list.active {
  opacity: 1;
  pointer-events: visible;
  transition: opacity 0.2s ease-in-out;
}
.header .header__languages-list-item {
  text-align: right;
  margin-bottom: 12px;
  box-sizing: border-box;
  font-size: 16px;
  color: #fff;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header .header__languages-list-item span {
  transition: color 0.2s ease-in-out;
}
.header .header__languages-list-item img {
  height: 12px;
  width: auto;
  display: block;
  margin-left: 5px;
}
.header .header__languages-list-item:hover span {
  color: #FABEFF;
  transition: color 0.2s ease-in-out;
}
.header .header__button {
  position: relative;
}
.header .header__button a {
  border: none;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  opacity: 0.9;
  text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
  color: #fff;
  background: linear-gradient(135deg, #40DDFF 0%, #14BAE3 19.24%, #13B1E6 68.64%, #11AADF 81.77%, #0B98C5 100%);
  border-radius: 9.10004px;
  padding: 16px 25px;
  box-sizing: border-box;
  cursor: pointer;
  text-decoration: none;
  white-space: nowrap;
}
.header .header__button::before {
  content: '';
  position: absolute;
  top: -7px;
  left: -53px;
  width: 304px;
  height: 79px;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
  background: linear-gradient(90.3deg, #22C7FF 2.07%, #ED1EFF 95.15%);
  filter: blur(66px);
}
.header .header__button:hover::before {
  opacity: 0.3;
  transition: opacity 0.4s ease-in-out;
}
@media all and (min-width: 1025px) {
  .header .header__body {
    justify-content: space-between;
    align-items: center;
    padding-bottom: 80px;
  }
}
@media all and (max-width: 1024px) {
  .header .header__body {
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
  }
  .header .header__nav {
    display: none;
  }
  .header .header__button {
    display: none;
  }
}
